import axios from "axios";

let HOSTNAME = "https://aqueous-temple-87066.herokuapp.com";
export default HOSTNAME = "https://aqueous-temple-87066.herokuapp.com";

export let check_loggedin = () => {
  if (localStorage.getItem("auth-token") === null) return false;
  else return true;
};

export let setToken = (token) => {
  localStorage.setItem("auth-token", token);
};

export let getToken = () => {
  return localStorage.getItem("auth-token");
};
export let removeToken = () => {
  localStorage.removeItem("auth-token");
};
