import React, { Component } from "react";
import "./Post.css";
import SinglePost from "./SinglePost";
import HOSTNAME, { getToken } from "../api/hostname";
import Loader from "./Loader";
import axios from "axios";

class MyPosts extends Component {
  state = {
    posts: [],
    username: null,
    isLoading: true,
  };
  componentDidMount() {
    this.fetchPosts();
  }
  render() {
    return (
      <div className="main">
        <div className="d-flex justify-content-center">
          <Loader isActive={this.state.isLoading} />
        </div>
        {this.state.posts.map((value) => (
          <SinglePost post={value} key={value.id}></SinglePost>
        ))}
      </div>
    );
  }

  fetchPosts = async () => {
    await this.fetchUserDetails();
    let fetch_url = HOSTNAME + "/post/?search=" + this.state.username;
    let header = {
      Authorization: "Token " + getToken(),
    };
    await axios
      .get(fetch_url, {
        headers: header,
      })
      .then((response) => {
        this.setState({ posts: response.data });
      })
      .catch((error) => {
        // window.alert("Fetching" + error.message);
      });
    this.setState({ isLoading: false });
  };

  fetchUserDetails = async () => {
    let url = HOSTNAME + "/account/user/";
    let header = {
      Authorization: "Token " + getToken(),
    };

    await axios
      .get(url, {
        headers: header,
      })
      .then((response) => {
        this.setState({ username: response.data.username });
      })
      .catch((error) => {
        // window.alert(error.message);
      });
  };
}

export default MyPosts;
