import React, { Component } from "react";
import "./Post.css";
import "./Navigation.css";
import { act } from "react-dom/test-utils";
import HOSTNAME, { getToken } from "../api/hostname";
import axios from "axios";
class SinglePost extends Component {
  state = {
    total_likes: null,
    liked: null,
  };
  constructor(props) {
    super(props);
    this.state.total_likes = props.post.total_likes;
    this.state.liked = props.post.liked;
    // this.update_likes();
  }
  get_time = () => {
    let uploaded_on = this.props.post.uploaded_on;
    let date = new Date(uploaded_on);
    let months = [
      "Jan",
      "Feb",
      "Mar",
      "Apr",
      "May",
      "Jun",
      "Jul",
      "Aug",
      "Sept",
      "Oct",
      "Nov",
      "Dec",
    ];
    return `${date.getDate()}-${months[date.getMonth()]}-${date.getFullYear()}`;
  };
  handle_likes = (event) => {
    if (this.state.liked === "False") {
      this.like("Like");
    } else {
      this.like("Unlike");
    }
  };
  like = (action) => {
    let like_url = this.props.post.like_url;
    let header = {
      Authorization: "Token " + getToken(),
    };
    if (action === "Like") {
      axios
        .post(like_url, null, {
          headers: header,
        })
        .then((response) => {
          // console.log(response.data);
        })
        .catch((error) => {
          // console.log(error.response);
        });
      this.setState({
        liked: "True",
        total_likes: this.state.total_likes + 1,
      });
    } else {
      axios
        .delete(like_url, {
          headers: header,
        })
        .then((response) => {
          // console.log(response.data);
        })
        .catch((error) => {
          // console.log(error.response);
        });
      this.setState({
        liked: "False",
        total_likes: this.state.total_likes - 1,
      });
    }
  };
  getImg = () => {
    if (this.state.liked === "True") return "img/heart-red.svg";
    else return "img/heart.svg";
  };
  update_likes = async () => {
    let fetch_url = HOSTNAME + "/post/" + this.props.post.id;
    let header = {
      Authorization: "Token " + getToken(),
    };

    await axios
      .get(fetch_url, {
        headers: header,
      })
      .then((response) => {
        this.setState({ total_likes: response.data.total_likes });
      })
      .catch((error) => {
        // window.alert("Fetching" + error.message);
      });
    window.setInterval(this.update_likes, 10000);
  };
  render() {
    return (
      <div className="d-flex flex-column  mt-2">
        <div className="d-flex pl-2">
          <img src="img/hacker.png" alt="user" className="post-profile-icon" />
          <div className="d-flex flex-row align-items-center">
            <a
              href={this.props.post.author_username}
              className="post-author-link"
            >
              <span className="post-author-name">
                {this.props.post.author_username}
              </span>
            </a>
            <span className="post-time text-muted">{this.get_time()}</span>
          </div>
        </div>
        <div className="d-flex m-2">{this.props.post.body}</div>
        {this.props.post.image && (
          <div className="d-flex image-responsive">
            <img
              src={this.props.post.image}
              alt=""
              className="post-image flex-grow-1 m-2"
            />
          </div>
        )}

        <div className="d-flex flex-row justify-content-center border-bottom border-line mt-2">
          <button
            onClick={this.handle_likes}
            className="like-btn"
            onClick={this.handle_likes}
          >
            <div className="d-flex flex-row align-items-center">
              <span className="text-muted">{this.state.total_likes}</span>
              <img alt="like" src={this.getImg()} className="ml-3 like-img" />
            </div>
          </button>
        </div>
      </div>
    );
  }
}

export default SinglePost;
