import React, { Component } from "react";
import "./Post.css";
import HOSTNAME, { getToken } from "../api/hostname";
import axios from "axios";
import Loader from "./Loader";

class Profile extends Component {
  state = {
    detail: {},
    isLoading: true,
  };
  componentWillMount() {
    this.fetchUserDetails();
  }
  render() {
    return (
      <div className="main d-flex flex-column align-items-center mt-4 border-bottom pb-3">
        <Loader isActive={this.state.isLoading} />
        {!this.state.isLoading && (
          <React.Fragment>
            <img
              src="img/hacker.png"
              alt="profile_pic"
              className="profile-avatar-image rounded-circle border border-black"
            />
            <span className="post-author-name mt-2">
              {this.state.detail.first_name}
            </span>
            <span className="text-muted post-time">
              @{this.state.detail.username}
            </span>

            <div className="mt-2">
              <span>
                <img
                  src="img/calendar.svg"
                  alt=""
                  className="profile-joined-icon ml-2 "
                />
              </span>
              <span className="ml-2 text-muted">{this.get_joined_time()}</span>
            </div>
          </React.Fragment>
        )}
      </div>
    );
  }
  get_joined_time = () => {
    let joined_on = this.state.detail.date_joined;
    let date = new Date(joined_on);
    let months = [
      "Jan",
      "Feb",
      "Mar",
      "Apr",
      "May",
      "Jun",
      "Jul",
      "Aug",
      "Sept",
      "Oct",
      "Nov",
      "Dec",
    ];
    return `${date.getDate()}-${months[date.getMonth()]}-${date.getFullYear()}`;
  };
  componentDidMount() {}
  fetchUserDetails = async () => {
    let url = HOSTNAME + "/account/user/";
    let header = {
      Authorization: "Token " + getToken(),
    };

    await axios
      .get(url, {
        headers: header,
      })
      .then((response) => {
        this.setState({ detail: response.data, isLoading: false });
      })
      .catch((error) => {
        // window.alert(error.message);
      });
  };
}

export default Profile;
